body {
  color: black;
  background:#FF8A80;
  display: flex;
  justify-content: center;
 }
 div {
  display: flex;
  flex-direction: row;
  text-align: center;
  align-items: center;
  padding: 30px;
 }
 input {
  width: 0.1px;
  height: 0.1px;
  opacity: 0;
  overflow: hidden;
  position: absolute;
  z-index: -1;
 }
 button,
 label,
 ul {
  width: 300px;
  box-sizing: border-box;
  border: solid 1px white;
  font-size: 24px;
  margin: auto;
  padding: 10px;
 }
 img {
  width: 300px;
  /* margin-bottom: 25px; */
 }
 ul {
  list-style: none;
  padding: 10px;
  /* margin-bottom: 25px; */
 }